body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  top: 0;
  padding: 10px;
  background-color: black;
  color: white;
  text-align: center;
}

.main-section {
  display: flex;
  flex-wrap: wrap;
}

.sidebar {
  width: 25%;
  background: gray;
  height: 80vh;
  text-align: center;
}

.main {
  width: 75%;
  height: 80vh;
  text-align: center;
}

.footer {
  padding: 5px;
  background-color: black;
  color: white;
  text-align: center;
}

button {
  padding: 10px;
  margin: 10px;
  border: none;
}

.color-button {
  background-color: green;
  color: white;
}

.color-button:hover {
  background-color: blue;
}

.navlink {
  margin: 5px;
  padding: 5px;
  background-color: white;
  color: black;
  text-decoration: none;
}

.box-page {
  cursor: pointer;
}

.active {
  background-color: brown;
}

.box-page {
  margin: 0 auto;
}

.btn-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

input {
  display: block;
  padding: 10px;
  margin: 10px auto;
  box-sizing: border-box;
}

.red {
  color: red;
}

.blue {
  color: blue;
}

.gray {
  color: gray;
}

.pink {
  color: pink;
}

.green {
  color: green;
}

.yellow {
  color: yellow;
}